import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import Button from '../components/Button';
import Typography from './Typography.js';
import ProductHeroLayout from './ProductHeroLayout';

const backgroundImage =
    //logan-volleyball
    //'https://www.sigtauzetasigma.org/uploads/1/1/7/1/117191759/background-images/20682404.jpg'
    //hiking
    'https://images.unsplash.com/photo-1486210284477-e900ad8a6820?ixlib=rb-1.2.1&auto=format&fit=crop&w=1504&q=80';

const styles = theme => ({
    background: {
        backgroundImage: `url(${backgroundImage})`,
        backgroundColor: '0f07af', // Average color of the background image.
        backgroundPosition: 'center',
    },
    button: {
        minWidth: 200,
    },
    h5: {
        marginBottom: theme.spacing.unit * 4,
        marginTop: theme.spacing.unit * 4,
        [theme.breakpoints.up('sm')]: {
            marginTop: theme.spacing.unit * 10,
        },
    },
    more: {
        marginTop: theme.spacing.unit * 2,
    },
});

function ProductHero(props) {
    const { classes } = props;

    return (
        <div class="Hero">
        <ProductHeroLayout backgroundClassName={classes.background}>
            {/* Increase the network loading priority of the background image. */}
            <img style={{ display: 'none' }} src={backgroundImage} alt="" />
            
            <Typography color="inherit" align="center" variant="h5" className={classes.h5}>
                LEARNING - INTEGRITY - EXCELLENCE - LEADERSHIP - CITIZENSHIP - BROTHERHOOD
      </Typography>
            <Button
                color="secondary"
                variant="contained"
                size="large"
                className={classes.button}
                component={linkProps => (
                    <Link {...linkProps} href="../pages/Join.js" variant="button" />
                )}
            >
                <span class="join">
                Join Now
                </span>
      </Button>
            <Typography variant="body1" color="inherit" className={classes.more}>
                Start your journey
      </Typography>
            </ProductHeroLayout>
            </div>
    );
}

ProductHero.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ProductHero);