import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Home from '@material-ui/icons/Home';
import Contact from '@material-ui/icons/MoveToInbox';
import Lock from '@material-ui/icons/Lock';
import Join from 'mdi-material-ui/AccountPlus';
import Outreach from 'mdi-material-ui/HomeGroup'
import School from '@material-ui/icons/School'
import Rose from '@material-ui/icons/SpaOutlined'

const styles = {
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
};
const menu = [{ text: 'Home', icon: <Home /> }, { text: 'Join Now', icon: <Join /> }, { text: 'Contact Us', icon: <Contact /> }, { text: 'Outreach', icon: <Outreach/>}, { text: 'Alumni', icon: <School/>}, { text: 'White Rose', icon: <Rose/> }]
const memberMenu = [{text: 'Members', icon: <Lock />}]
class SwipeableTemporaryDrawer extends React.Component {
 
  render() {
    const { classes } = this.props;

    const sideList = (
      <div className={classes.list}>
        <List>
          {menu.map((listItem, index) => (
            <ListItem button key={listItem.text}>
              <ListItemIcon>{listItem.icon}</ListItemIcon>
              <ListItemText primary={listItem.text} />
            </ListItem>
          ))}
        </List>
        <Divider />
        <List>
          {memberMenu.map((listItem, index) => (
            <ListItem button key={listItem.text}>
              <ListItemIcon>{listItem.icon}</ListItemIcon>
              <ListItemText primary={listItem.text} />
            </ListItem>
          ))}
        </List>
      </div>
    );


    return (
      <div>
        
        <SwipeableDrawer
          open={this.props.open}
          onClose={this.props.onToggleDrawer('left', false)}
          onOpen={this.props.onToggleDrawer('left', true)}
        >
          <div
            tabIndex={0}
            role="button"
            onClick={this.props.onToggleDrawer('left', false)}
            onKeyDown={this.props.onToggleDrawer('left', false)}
          >
            {sideList}
          </div>
        </SwipeableDrawer>
      </div>
    );
  }
}

SwipeableTemporaryDrawer.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(SwipeableTemporaryDrawer);